<template>
  <div style="height: 1600px;">
    <div class="tb-header" style="height: auto">
      <span>日报表</span>
      <el-form label-width="80px">
        <el-row>
          <el-col :span="6">
            <el-form-item label="传感器">
              <el-select v-model="SelectedID" placeholder="请选择">
                <el-option
                  v-for="(item,index) in SensorList"
                  :key="index"
                  :label="item.Name"
                  :value="item.ID"
                ></el-option>
              </el-select>

            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="统计时间">
              <el-date-picker type="date" placeholder="统计时间"   v-model="StartTime"></el-date-picker>

            </el-form-item>
          </el-col>

          <el-col :span="6">
            <el-form-item  >
            <el-button @click="Query">查询</el-button>
              <el-button  v-print="'#ToPrint'" class="el-button--primary">打印</el-button>
            </el-form-item>
          </el-col>
        </el-row>

      </el-form>
    </div>
    <!--<div id="top" v-if="IsHidden"></div>-->
    <div  style="height: auto;text-align: center;" id="ToPrint">
      <table label-width="80px" style="width:1024px;text-align: center;"  border="1" cellspacing="0" cellpadding="0">
        <tr >
          <td style="border: 0">
              <label style="text-align: center;font-size: 24px;font-weight: bold">{{SelectedName+"日报表" }}</label>
          </td>
        </tr>
        <tr>
          <td  style="text-align: right;height: 50px" >
            <label>{{getdate(StartTime)}}</label>
          </td>
        </tr>
        <!--<tr style="height: 200px;">-->
          <!--<div class="chart" id="Chart" style="width:100%;height:200px;"></div>-->
        <!--</tr>-->
        <tr style="text-align: left;">
          <td style="text-align: left;height: 50px;">
            <label >监测数据</label>
          </td>
        </tr>
        <tr style="text-align: left;height: 100px;">
          <td style="text-align: left;">
            <label  style="text-align: left;margin-right: 200px;margin-left: 200px;">{{'最高值:'+MaxValue}}</label>
            <label  style="text-align: left;margin-right: 200px;">{{'最低值:'+MinValue}}</label>
            <label>{{'平均值:'+AverageValue}}</label>
          </td>
        </tr>
        <tr style="text-align: left;height: 50px">
          <td style="text-align: left;">
            <label >监测结论</label>
          </td>
        </tr>
        <tr style="height: 400px;">

        </tr>
        <tr style="text-align: left;height: 50px">
          <td style="text-align: left;">
          <label style="text-align: left;">区队意见</label>
          </td>
        </tr>
        <tr style="height: 400px;">

        </tr>
        <tr>
          <td>
<label style="text-align: left;margin-right: 200px;height: 50px">分管领导：</label>

            <label style="text-align: center;margin-right: 200px;">部门签字：</label>

            <label style="text-align: right;">报表人：</label>
          </td>
        </tr>
      </table>
    </div>

  </div>
</template>
<script>
  import axios from 'axios'
  import echarts from 'echarts'
  import {TimeFormat} from '../assets/js/Common'
  //  import {GetStore, SetStore} from '../vuex/store'
  export default {
    data() {
      return {
        DataList: [],
        StartTime: null,
        EndTime: null,
        SensorList: [],
        SelectedID: 0,
        SelectedName: '',
        MaxValue:0,
        MinValue:0,
        AverageValue:0,
      }
    },
    methods: {
      getdate(d){
        var date = new Date(d)
        var year = date.getFullYear()
        var month = date.getMonth() + 1
        var day = date.getDate()
        if (month < 10) {
          month = '0' + month
        }
        if (day < 10) {
          day = '0' + day
        }
        return year + '-' + month + '-' + day
      },
      Query() {
        //this.IsHidden=true
        var query = {
          ID: this.SelectedID,
          StartTime: this.StartTime,
          EndTime:this.EndTime,
        }
        var sensor = null
        for (var i = 0; i < this.SensorList.length; i++) {
          if (this.SensorList[i].ID === this.SelectedID) {
            sensor = this.SensorList[i]
            this.SelectedName=sensor.Name
            break
          }
        }
        if(sensor==null)
          return
        //this.chart = echarts.init(document.getElementById('Chart'))
        axios.post('/api/querySensorHistory', query).then(response => {
          this.DataList = response.data.Data
        var max=0
        var min=10000
        var average=0
          for (var i = 0; i < this.DataList.length; i++) {
            if (max < this.DataList[i].FirstValue) {
              max = this.DataList[i].FirstValue
            }

            if (min > this.DataList[i].FirstValue) {
              min = this.DataList[i].FirstValue
            }
            average=average+this.DataList[i].FirstValue
          }
          this.MaxValue=max/10
          if(min<10000)
          this.MinValue=min/10
          if(this.DataList.length>0)
          this.AverageValue=average/this.DataList.length/10

          // var x = new Array()
          // var y = new Array()
          // var z = new Array()
          // for (var i = 0; i < this.DataList.length; i++) {
          //   x.push(TimeFormat(this.DataList[i].CreateTime))
          //   y.push(this.DataList[i].FirstValue/10)
          //   z.push(this.DataList[i].SecondValue/10)
          // }
          // this.chart.clear()
          // if(sensor.SensorType === 0) {
          //   this.chart.setOption({
          //     tooltip: {},
          //     xAxis: {
          //       data: x
          //     },
          //     yAxis: {
          //       name: (sensor.SensorType === 0) ? '兆帕' : '千牛'
          //     },
          //     series: [{
          //       name: '前柱',
          //       type: 'line',
          //       data: y
          //     },
          //       {
          //         name: '后柱',
          //         type: 'line',
          //         data: z
          //       }]
          //   })
          // }else{
          //   this.chart.setOption({
          //     //title: {text: sensor.Name + '历史数据'},
          //     tooltip: {},
          //     xAxis: {
          //       data: x
          //     },
          //     yAxis: {
          //       name: (sensor.SensorType === 0) ? '兆帕' : '千牛'
          //     },
          //     series: [{
          //       name: '前柱',
          //       type: 'line',
          //       data: y
          //     }]
          //   })
          // }

        })
      }
    },
    mounted (){
    axios.post('/api/getAllSensor', this.QueryForm).then(response => {
      this.SensorList = response.data.Data
      if (this.SensorList.length > 0) {
        this.SelectedID = this.SensorList[0].ID
      }
      this.StartTime = new Date()
      this.EndTime = new Date()
      //this.Query()
    })
    }
  }
</script>




